import Vue from 'vue'
import VueRouter from 'vue-router'
import LocateTable from '@/components/locate/LocateTable.vue'
import LocateDetails from '../pages/locate/LocateDetails.vue'
import LocatePage from '../pages/locate/LocatePage.vue'
import HomePage from '../pages/HomePage.vue'

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/ff',
      name: 'locate',
      component: LocateTable,
      props: true
    },

    //Home
    {
      path: '/',
      component: HomePage,
    },

    //Locate
    {
      path: '/locatepage',
      component: LocatePage,
    },
    {
      path: '/locatedetails/:id/:address',
      component: LocateDetails,
    }


  ],
})

export default router

